<template>
    <layout title="Facturas">
        <h1 class="mb-8 font-bold text-3xl">Facturas</h1>
        <div class="mb-6 flex justify-between items-center">
            <search-filter v-model="form.search" class="w-full max-w-sm mr-4" @reset="reset">
                <label class="block text-grey-darkest">Tipo de factura:</label>
                <select v-model="form.bill_type" class="mt-1 w-full form-select">
                    <option :value="null" />
                    <option value="CONTADO">Contado</option>
                    <option value="CREDITO">Credito</option>
                </select>
                <label class="block text-grey-darkest mt-4">Estado:</label>
                <select v-model="form.status" class="mt-1 w-full form-select">
                    <option :value="null" />
                    <option value="PAGADA">Pagada</option>
                    <option value="EN PROCESO">En Proceso</option>
                    <option value="EXPIRADA">Expirada</option>
                    <option value="CANCELADA">Cancelada</option>
                </select>
            </search-filter>
            <inertia-link class="btn-green" :class="{'hidden':bills.data.length === 0}" :href="route('invoice.bills.create', organization.slug)">
                <span>Crear</span>
                <span class="hidden md:inline">Factura</span>
            </inertia-link>
        </div>
        <div class="bg-white rounded shadow overflow-x-auto">
            <table class="w-full whitespace-no-wrap">
                <tr class="text-left font-bold">
                    <th class="px-6 pt-6 pb-4">No. Factura</th>
                    <th class="px-6 pt-6 pb-4">Cliente</th>
                    <th class="px-6 pt-6 pb-4">Fecha</th>
                    <th class="px-6 pt-6 pb-4">Importe</th>
                    <th class="px-6 pt-6 pb-4" colspan="2">Estado</th>
                </tr>
                <tr v-for="bill in bills.data" :key="bill.id" class="hover:bg-grey-lightest focus-within:bg-grey-lightest">
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center focus:text-green" :href="route('invoice.bills.edit', {'slug':organization.slug, 'bill':bill.id})">
                            {{ bill.id }}
                            <icon v-if="bill.deleted_at" name="trash" class="flex-no-shrink w-3 h-3 fill-grey ml-2" />
                        </inertia-link>
                    </td>
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center" :href="route('invoice.bills.edit', {'slug':organization.slug, 'bill':bill.id})">
                            <div class="flex flex-col">
                                {{ bill.client ? `${bill.client.name} ${bill.client.last_name}` : 'Al contado' }}
                                <small class="pt-2 text-grey-dark">Debe: ${{ bill.payments }}</small>
                            </div>
                        </inertia-link>
                    </td>
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center" :href="route('invoice.bills.edit', {'slug':organization.slug, 'bill':bill.id})">
                            {{ bill.created_at }}
                        </inertia-link>
                    </td>
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center" :href="route('invoice.bills.edit', {'slug':organization.slug, 'bill':bill.id})">
                            <div class="flex flex-col">
                                {{ bill.total }}
                                <small v-if="bill.expired_at" class="pt-2 text-grey-dark">Exp. {{ bill.expired_at }}</small>
                            </div>
                        </inertia-link>
                    </td>
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center" :href="route('invoice.bills.edit', {'slug':organization.slug, 'bill':bill.id})">
                            <div v-if="bill.status == 'Pagada'" class="bg-green-lightest text-green-darker text-sm font-bold rounded-full py-2 px-4">{{ bill.status }}</div>
                            <div v-else-if="bill.status == 'En Proceso'" class="bg-blue-lightest text-blue-darker text-sm font-bold rounded-full py-2 px-4">{{ bill.status }}</div>
                            <div v-else-if="bill.status == 'Expirada'" class="bg-yellow-lightest text-yellow-darker text-sm font-bold rounded-full py-2 px-4">{{ bill.status }}</div>
                            <div v-else-if="bill.status == 'Cancelada'" class="bg-red-lightest text-red-darker text-sm font-bold rounded-full py-2 px-4">{{ bill.status }}</div>
                        </inertia-link>
                    </td>
                    <td class="border-t w-px">
                        <inertia-link class="px-4 flex items-center" :href="route('invoice.bills.edit', {'slug':organization.slug, 'bill':bill.id})" tabindex="-1">
                            <icon name="cheveron-right" class="block w-6 h-6 fill-grey" />
                        </inertia-link>
                    </td>
                </tr>
                <tr v-if="bills.data.length === 0" class="">
                    <td class="border-t px-6 py-4" colspan="6">
                        <div class="text-center flex flex-wrap justify-center">
                            <svg class="w-full h-64" id="a5ea60a6-7e3a-43b2-b481-a6d571d01698" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" width="1020" height="734.19614" viewBox="0 0 1020 734.19614"><title>reading</title><ellipse cx="502" cy="701.69614" rx="259" ry="32.5" fill="#3f3d56"/><path d="M1030.12537,506.7105,930,554.09807l88.6153-66.2619a500.05681,500.05681,0,0,0-48.38385-63.66846L536,532.09807,928.2334,382.47454a499.98032,499.98032,0,0,0-56.95764-43.41419L209.60925,445.6375a495.88,495.88,0,0,0-108.6029,309.46057h995.9873A495.61329,495.61329,0,0,0,1030.12537,506.7105Z" transform="translate(-90 -82.90193)" fill="#f2f2f2"/><path d="M817.12317,308.29344A495.97179,495.97179,0,0,0,599,258.09807c-153.82965,0-291.36139,69.75269-382.71033,179.34869C372.71606,403.17229,645.06689,343.84123,817.12317,308.29344Z" transform="translate(-90 -82.90193)" fill="#f2f2f2"/><circle cx="825.07983" cy="258.36001" r="14.92017" fill="#38d39f" opacity="0.4"/><circle cx="797.46639" cy="265.48606" r="10.46639" fill="#38d39f" opacity="0.4"/><circle cx="118.7521" cy="297.89782" r="31.2479" fill="#38d39f" opacity="0.4"/><circle cx="60.92017" cy="312.82219" r="21.92017" fill="#38d39f" opacity="0.4"/><rect y="671.19614" width="1020" height="2" fill="#2f2e41"/><path d="M994.44094,754.09807,915,636.09807s120.30709,42,106.65354,118Z" transform="translate(-90 -82.90193)" fill="#3f3d56"/><path d="M997.55906,754.09807l79.44094-118s-120.30709,42-106.65354,118Z" transform="translate(-90 -82.90193)" fill="#38d39f"/><path d="M298,215.6728c0-6.49686-9.2638-11.76359-20.69132-11.76359-.22617,0-.44993.00558-.67436.00968-3.44726-10.56733-19.74316-18.57-39.32885-18.57-16.66388,0-30.94586,5.79338-36.96,14.03014a52.407,52.407,0,0,0-13.61878-1.74372C169.20451,197.63529,155,205.711,155,215.6728c0,9.04918,11.72185,16.54056,27.00353,17.83751,11.10464,2.03037,28.45034,3.337,47.945,3.337,33.09671,0,57.3139-6.31342,57.92127-11.059C293.93431,223.73728,298,219.97573,298,215.6728Z" transform="translate(-9